/* Styles généraux */
body {
    margin: 0;
    font-family: Arial, sans-serif;
}

.feature-pinkname{
    color: #ff007f;
    font-weight: bold;
}

.homepage-features-videos{
    align-content: center;
    justify-content: center;
    text-align: center;
}

.formheader{
    text-align: center;
}
.feature-card h3 {
    margin-bottom: 10px; /* Ajoute un espace en dessous du titre */
}

.feature-card {
    display: block; /* Rend le contenu vertical */
    text-align: center; /* Centre le texte et l'image */
    padding: 20px; /* Ajoute un peu d'espace autour */
}

.feature-card p {
    text-align: justify; /* Justifie le texte */
    margin-top: 10px; /* Ajoute un espace au-dessus du texte de présentation */
}

.feature-card img {
    max-height: 400px; /* Ajuste cette valeur selon les besoins */
    align-content: center;
    justify-content: center;
    text-align: center;
    max-width: 100%;
    max-height: 400px; /* Ajuste cette valeur selon les besoins */
    width: auto;
    height: auto;
    object-fit: contain; /* Utilise 'cover' si tu veux remplir tout l'espace */
    border-radius: 8px; /* Pour arrondir les coins, optionnel */
}


.homepage-container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0;
    background-color: #252122;
    color: #fff;
}

/* Menu principal horizontal pour les grands écrans */
.homepage-menu {
    display: flex;
    justify-content: space-around;
    margin-bottom: 20px;
}

/* Style pour le menu Kendo */
.k-menu {
    background-color: #252122;
    color: #fff;
    font-size: 14px;
    display: flex; /* Menu horizontal sur grands écrans */
    gap: 1em;
}
.menu-open {
    visibility: visible;
    opacity: 1;
    transition: opacity 0.3s ease-in-out;
}

.menu-closed {
    visibility: hidden;
    opacity: 0;
    transition: opacity 0.3s ease-in-out;
}
.k-menu-item {
    padding: 5px 10px;
    font-size: 13px;
    color: #fff;
    white-space: nowrap;
    cursor: pointer;
}

.k-menu-item .k-link {
    display: flex;
    align-items: center;
    cursor: pointer;
}

.k-menu-item:hover {
    background-color: #ff007f;
}

.k-menu-item.k-state-selected {
    background-color: #ff66a3;
}

.k-menu-popup {
    background-color: #252122;
    color: #fff;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

/* Sous-menus */
.k-menu-item .k-menu-group {
    background-color: #252122;
    color: #fff;
}

.k-menu-item .k-menu-group .k-menu-item {
    padding: 5px 10px;
    background-color: #000;
    font-size: 13px;
    color: #fff;
    white-space: nowrap;
    cursor: pointer;
}

.k-menu-item .k-menu-group .k-menu-item:hover {
    background-color: #ff007f;
}

.k-menu-item .k-menu-group .k-menu-item.k-state-selected {
    background-color: #ff66a3;
}

/* Social media */
.social-media {
    display: flex;
    align-items: center;
    gap: 1em;
}

.social-media p {
    margin: 0;
    font-size: 1.1em;
    background-color: #252122;
    color: #fff;
}

.social-button {
    color: #ff007f;
    transition: color 0.3s ease;
    padding: 10px;
}

.social-button:hover {
    color: #000;
}

/* Header */
.homepage-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1em;
    background-color: #252122;
    color: #fff;
}

.homepage-logo {
    width: 150px;
    height: auto;
}

/* Bannière */
.homepage-banner {
    margin-bottom: 20px;
}

.slider {
    width: 100%;
    overflow: hidden;
    height: auto;
}

.slides {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 10px;
}

.banner-image {
    width: 100%;
    height: auto;
    object-fit: contain;
}

/* Call to action */
.homepage-cta {
    text-align: center;
    margin: 2em 0;
}

.cta-button, .cta-button-secondary {
    background-color: #ff007f;
    color: #fff;
    padding: 0.75em 2em;
    margin: 0.5em;
    border: none;
    cursor: pointer;
}

.cta-button:hover, .cta-button-secondary:hover {
    background-color: #000;
}

/* Menu vertical pour les petits écrans */
@media (max-width: 600px) {
    .homepage-menu {
        flex-direction: column; /* Menu devient vertical sur petits écrans */
        align-items: flex-start;
    }

    .slider {
        grid-template-columns: repeat(2, 1fr);
    }

    .homepage-header {
        flex-direction: column;
        justify-content: center;
        text-align: center;
    }

    .homepage-logo {
        width: 100px;
        height: auto;
    }

    .homepage-banner {
        margin-bottom: 20px;
    }

    .homepage-cta {
        text-align: center;
    }
}

/* Chatbot styles */
.chatbot-container {
    position: fixed;
    bottom: 20px;
    right: 20px;
    z-index: 1000;
}

.chat-icon {
    background-color: #fff;
    border-radius: 50%;
    padding: 10px;
    cursor: pointer;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    transition: transform 0.3s ease;
}

.chat-icon:hover {
    transform: scale(1.1);
}

.chat-window {
    position: absolute;
    bottom: 60px;
    right: 0;
    background-color: #fff;
    padding: 15px;
    width: 300px;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.chat-window h4 {
    margin-top: 0;
    font-size: 18px;
    color: #333;
}

.chat-window input,
.chat-window textarea {
    width: 100%;
    margin-bottom: 10px;
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 5px;
    font-size: 14px;
}

.chat-window button {
    background-color: #ff007f;
    color: #fff;
    padding: 10px;
    width: 100%;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
}

.chat-window button:hover {
    background-color: #e6006b;
}
